<template>
  <div>
    <el-form :inline="true" :model="seasonForm" class="demo-form-inline">
      <el-form-item label="ËµõÂ≠£Âêç">
        <el-input v-model="seasonForm.name" placeholder="ËæìÂÖ•ËµõÂ≠£Âêç"></el-input>
      </el-form-item>
      <el-form-item label="ÂºÄÂßãÊó∂Èó¥">
        <el-input v-model="seasonForm.opentime"></el-input>
      </el-form-item>
      <el-form-item label="Êä•ÂêçÊòØÂê¶ÁªìÊùü">
        <el-radio-group v-model="seasonForm.is_over">
          <el-radio :label="true">Â∑≤ÁªìÊùü</el-radio>
          <el-radio :label="false">Êú™ÁªìÊùü</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="pushNewEvents">ÂèëÂ∏É</el-button>
      </el-form-item>
    </el-form>
    <!-- Ë°®Ê†º -->
    <el-table v-loading="loading" :data="events" border height="50vh">
      <el-table-column prop="id" label="Â∫èÂè∑" width="60"> </el-table-column>
      <el-table-column label="ÂêçÂ≠ó" width="180">
        <template slot-scope="scope">
          <span
            v-if="!scope.row.isEdit"
            @dblclick="toggleIsEdit(scope.row.isEdit, scope.$index)"
            >{{ scope.row.name }}</span
          >
          <el-input
            v-else
            v-model="scope.row.name"
            @blur="toggleIsEdit(scope.row.isEdit, scope.$index)"
          ></el-input>
        </template>
      </el-table-column>
      <el-table-column label="ÊòØÂê¶ÁªìÊùü" width="180">
        <template slot-scope="scope">
            <el-switch
              v-model="scope.row.is_over"
              active-color="#ff4949"
              inactive-color="#409EFF"
              active-text="Â∑≤ÁªìÊùü"
              inactive-text="ËøõË°å‰∏≠"
            >
            </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="ÂºÄÂßãÊó∂Èó¥" width="250">
        <template slot-scope="scope">
          <template v-if="scope.row.opentime === null">
            <el-input v-model="scope.row.opentime"></el-input>
          </template>
          <template v-else>
            <span
              v-if="!scope.row.isEdit"
              @dblclick="toggleIsEdit(scope.row.isEdit, scope.$index)"
              >{{ scope.row.opentime }}</span
            >
            <el-input
              v-else
              v-model="scope.row.opentime"
              @blur="toggleIsEdit(scope.row.isEdit, scope.$index)"
            ></el-input>
          </template>
        </template>
      </el-table-column>
      <el-table-column label="Êìç‰Ωú" width="auto">
        <template slot-scope="scope">
          <el-button type="warning" size="mini" @click="updateItem(scope.row)"
            >Êõ¥Êñ∞</el-button
          >
          <el-button
            type="danger"
            size="mini"
            @click="deleteItem(scope.row.name)"
            >Âà†Èô§</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import {
  pushNewEvents,
  getAllEvents,
  deleteEvents,
  updateEvents,
} from "@/api/gameSeason/index";
export default {
  name: "SetSeason",
  data() {
    return {
      seasonForm: {
        name: "",
        is_over: false,
        opentime: "xxxx-xx-xxT00:00:00",
      },
      events: [],
      loading: false,
    };
  },
  mounted() {
    this.initEvents();
  },
  methods: {
    pushNewEvents() {
      pushNewEvents(
        this.seasonForm.name,
        this.seasonForm.is_over,
        this.seasonForm.opentime
      )
        .then((res) => {
          this.$message.success(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    initEvents() {
      this.loading = true;
      getAllEvents()
        .then((res) => {
          let { data } = res;
          this.events = data;
          for (var i = 0; i < data.length; i++) {
            const source = {
              isEdit: false,
            };
            this.events[i] = Object.assign(this.events[i], source);
          }
          console.log("üêï", this.events);
          setTimeout(() => {
            this.loading = false;
          }, 800);
        })

        .catch(() => {
          this.$message.error("ÊúçÂä°Âô®ÂºÇÂ∏∏ÔºåËØ∑ËÅîÁ≥ªÁÆ°ÁêÜÂëòÔºÅ");
          setTimeout(() => {
            this.loading = false;
          }, 800);
        });
    },
    deleteItem(name) {
      this.$confirm("ÊòØÂê¶Âà†Èô§ËµõÂ≠£?", "ÊèêÁ§∫", {
        confirmButtonText: "Á°ÆÂÆö",
        cancelButtonText: "ÂèñÊ∂à",
        type: "warning",
      })
        .then(() => {
          deleteEvents(name)
            .then(() => {
              this.$message.success("Âà†Èô§ÊàêÂäüÔºÅ");
              this.initEvents();
            })
            .catch(() => {
              this.$message.error("ÊúçÂä°Âô®ÂºÇÂ∏∏ÔºåËØ∑ËÅîÁ≥ªÁÆ°ÁêÜÂëòÔºÅ");
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "Â∑≤ÂèñÊ∂àÂà†Èô§",
          });
        });
    },
    toggleIsEdit(isEdit, index) {
      this.events[index].isEdit = !isEdit;
      this.events = JSON.parse(JSON.stringify(this.events));
    },
    updateItem(row) {
      updateEvents(row.name, row)
        .then(() => {
          this.$message.success("Êõ¥Êñ∞ÊàêÂäüÔºÅ");
          this.initEvents();
        })
        .catch(() => {
          this.$message.error("ÊúçÂä°Âô®ÂºÇÂ∏∏ÔºÅ");
        });
    },
  },
};
</script>

<style scoped lang="less"></style>
